/*** Main Navigation **********************/
.main_nav_list {
  display: none;
  list-style-type: none;
  position: absolute;
  z-index: 4;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0;
  margin: 0;
  overflow-y: auto;
  background-color: black;
}

.main_nav_list_item_link {
  display: block;
  width: 100%;
  padding: 2em 0;
  text-align: center;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 2px;
}

.main_nav_list_item_link_icon {
  display: none;
}

.main_nav_list_item_link_title {
  display: flex;
  justify-content: center;
  align-items: center;
}
.main_nav_list_item_link_title::before, .main_nav_list_item_link_title::after {
  opacity: 0;
  display: inline-block;
  content: '';
  width: 0;
  height: 4px;
  background-color: white;
  transition: width .25s, margin 1s, opacity .5s;
}
.main_nav_list_item_link_title::before {
  margin-right: 10px;
}
.main_nav_list_item_link_title::after {
  margin-left: 10px;
}

.main_nav_list_item_link:hover .main_nav_list_item_link_title:before {
  opacity: 1;
  width: 14px;
  margin-right: 20px;
}

.main_nav_list_item_link:hover .main_nav_list_item_link_title:after {
  opacity: 1;
  width: 14px;
  margin-left: 20px;
}

.js .main_nav-active .main_nav_list {
  display: block;
}

/*** Hamburger Icon **********************/
.main_nav_link {
  position: relative;
  display: inline-block;
  padding: 22px;
  height: 60px;
  z-index: 3;
}

.no-js .main_nav_link {
  display: block;
}

.main_nav_link:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.main_nav_icon {
  cursor: pointer;
  width: 16px;
  fill: black;
}

.main_nav_icon-top {
  transform-origin: 0px 19px;
}

.main_nav_icon-bottom {
  transform-origin: 0px 44px;
}

.main_nav_icon-top, .main_nav_icon-bottom {
  transition: transform .5s;
  transform: rotate(0);
}

.main_nav_icon-middle {
  transition: opacity .5s;
  opacity: 1;
}

.main_nav_close {
  text-align: right;
  padding: 1em;
  cursor: pointer;
}

.icon-white {
  fill: white;
}

.icon-black {
  fill: black;
}

