  flex-direction: row;
  flex-wrap: wrap;
}

.scrapbook_img {
  position: relative;
  flex-basis: 50%;
  line-height: 0;
  overflow: hidden;
}
.scrapbook_img:after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(255, 235, 59, 0.1);
}
.scrapbook_img:hover:after {
  background-color: transparent;
}

.scrapbook_img img {
  width: 100%;
  height: auto;
}

.scrapbook_container_btn-close, .scrapbook_img_info {
  display: none;
}

/** Active States *****************************/
.scrapbook_container-is_active {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 4;
}

.scrapbook_container-is_active .scrapbook_img_container {
  flex-wrap: nowrap;
  height: 100%;
}

.scrapbook_container-is_active .scrapbook_img {
  flex: 0 0 100%;
  height: inherit;
  cursor: default;
}
.scrapbook_container-is_active .scrapbook_img:after {
  content: none;
}

.scrapbook_container-is_active .scrapbook_img img {
  width: auto;
}

.scrapbook_container-is_active .scrapbook_img_info {
  display: block;
  position: absolute;
  width: 100%;
  padding: 1em;
  background-color: lightgrey;
  bottom: 0;
  z-index: 3;
}

.scrapbook_container-is_active .scrapbook_container_btn-close {
  display: block;
  width: 100%;
  position: absolute;
  cursor: pointer;
  z-index: 3;
  padding: 1em;
  text-align: right;
}
.scrapbook_container-is_active .scrapbook_container_btn-close:hover {
  background-color: rgba(0, 0, 0, 0.2);
}

