  width: 100%;
  text-align: right;
  line-height: 0;
  padding: 1em;
  background-color: transparent;
  transition: background-color .25s;
}

.modal-close:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.modal-close .icon-cross {
  display: inline-block;
  width: 10px;
  height: 10px;
}

.modal_main {
  position: fixed;
  left: 0;
  z-index: 5;
  background-color: #000;
  width: 90vw;
  height: 90vh;
  margin: 5vh 5vw;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  transition: all 1s;
  overflow: hidden;
  padding-bottom: 4em;
  background: linear-gradient(225deg, #2D2D2D 25%, transparent 25%) -25px 0, linear-gradient(135deg, #2D2D2D 25%, transparent 25%) 25px 0, linear-gradient(315deg, #2D2D2D 25%, transparent 25%), linear-gradient(45deg, #2D2D2D 25%, transparent 25%);
  background-size: 50px 50px;
  background-color: #2a2a2a;
}

.modal-active .modal_main {
  animation: modalMoveUp ease-out .5s;
}

