.modal_gallery_container {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
}

.modal_img_container {
  flex: 0 0 100%;
  width: 100%;
  height: 100%;
  padding-left: 1em;
  padding-right: 1em;
  overflow: hidden;
}

.modal_main_img_zoom_button {
  display: inline-block;
  opacity: 0;
  width: 30px;
  height: 30px;
  background: url("../img/icons.svg#zoom-in") no-repeat 0 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  z-index: 10;
}

.modal_main_img_zoom_button:hover {
  opacity: 1;
}

.modal_main_img {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  max-width: 100%;
  max-height: 100%;
  z-index: 1;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.5);
}

