.modal_main_content-outer {
  position: relative;
  flex-grow: 1;
  margin: 2em;
}

.modal_main_content-inner {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.modal_main_content_back_button, .modal_main_content_forward_button {
  cursor: pointer;
  position: absolute;
  display: block;
  padding-top: 8em;
  padding-bottom: 8em;
  z-index: 5;
  line-height: 0;
  top: 50%;
  margin-top: -136px;
}

.modal_main_content_back_button:hover, .modal_main_content_forward_button:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.modal_main_content_back_button {
  left: -2em;
  padding-left: .5em;
  padding-right: 1em;
}

.modal_main_content_forward_button {
  right: -2em;
  padding-left: 1em;
  padding-right: .5em;
}

.modal_main_sidebar {
  position: relative;
  bottom: -4em;
  background-color: #add8e6;
}

.modal_main_sidebar_content {
  padding: 1em;
  flex-basis: 4em;
  background-color: #add8e6;
  overflow: hidden;
  position: absolute;
  bottom: 0;
  height: 4em;
  width: 100%;
  z-index: 10;
  transition: height 1s;
}

.modal_main_sidebar_content-is_active {
  height: 14em;
  overflow: auto;
}

.modal_bg {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 1;
}

/** Zoomed in changes **/
.modal_main_content-zoomed_in {
  margin: 0;
  overflow: auto;
}

.modal_main_content-zoomed_in .modal_main_img {
  top: 0;
  left: 0;
  transform: inherit;
  max-width: inherit;
  max-height: inherit;
}

.modal_main_content-zoomed_in .modal_main_content_forward_button,
.modal_main_content-zoomed_in .modal_main_content_back_button {
  display: none;
}

.modal_main_content-zoomed_in .modal_main_img_zoom_button {
  background: url("../img/icons.svg#zoom-out") no-repeat 0 0;
  position: fixed;
  top: 0;
  left: 0;
  transform: none;
  z-index: 10;
}

@media screen and (min-width: 500px) {
  .modal-close {
    padding: 1em;
  }

  .modal-close .icon-close {
    width: 12px;
    height: 12px;
  }

  .modal_main_content_back_button {
    padding-left: 1em;
  }

  .modal_main_content_forward_button {
    padding-right: 1em;
  }
}
@media screen and (min-width: 800px) {
  .modal_main {
    flex-direction: row;
    padding-bottom: 0;
  }

  .modal_main_sidebar {
    position: static;
    flex-basis: 19em;
    overflow: auto;
  }

  .modal_main_sidebar_content {
    position: relative;
    padding: 2em;
    height: auto;
    overflow: auto;
  }
}
@media screen and (min-width: 1000px) {
  .modal_img_container {
    padding-left: 0;
    padding-right: 0;
  }
}
@media screen and (min-width: 1350px) {
  .modal_main {
    width: 70vw;
    margin-left: 15vw;
    margin-right: 15vw;
  }
}
@keyframes modalMoveUp {
  from {
    bottom: -5%;
  }
  to {
    bottom: 0;
  }
}
